  padding-top: 312px;
}
@media (max-width: 1439px) {
  .info__container {
    padding: 312px 111px 0;
  }
}
@media (max-width: 1199px) {
  .info__container {
    min-height: 679px;
    padding: 195px 236px 0;
  }
}
@media (max-width: 767px) {
  .info__container {
    min-height: 621px;
    padding: 181px 15px 0;
  }
}
.info__container h2 {
  display: flex;
  justify-content: center;
  margin: 0;
  margin-right: 17px;
  margin-bottom: 18px;
  font-size: 35px;
  line-height: 35px;
  font-weight: 800;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .info__container h2 {
    margin-right: 0;
    margin-bottom: 19px;
    font-size: 25px;
    line-height: 25px;
  }
}
@media (max-width: 767px) {
  .info__container h2 {
    margin-bottom: 15px;
    font-size: 22px;
    line-height: 22px;
  }
}
.info__container p {
  display: flex;
  justify-content: center;
  margin: 0;
  margin-right: 18px;
  margin-bottom: 40px;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .info__container p {
    margin-right: 0;
    margin-bottom: 29px;
    text-align: center;
  }
}
@media (max-width: 767px) {
  .info__container p {
    margin-bottom: 25px;
    font-size: 12px;
    line-height: 18px;
  }
}
.info__container form {
  display: grid;
  grid-template-columns: 1fr 1fr minmax(238px, auto);
  gap: 30px;
}
@media (max-width: 1199px) {
  .info__container form {
    display: flex;
    flex-direction: column;
    gap: unset;
    padding: 0 15px;
  }
}
@media (max-width: 767px) {
  .info__container form {
    padding: 0;
  }
}
.info__container input {
  min-width: 340px;
  width: 100%;
  padding: 22px 30px 21px;
  font-size: 16px;
  line-height: 16px;
  background-color: #ffffff;
  border: 1px solid rgba(2, 102, 193, 0.1);
}
@media (max-width: 1199px) {
  .info__container input {
    min-width: 266px;
    padding: 15px;
    font-size: 14px;
    line-height: 14px;
  }
}
.info__container input::placeholder {
  color: #2d383f;
  opacity: 0.5;
  transition: opacity 0.3s ease;
}
.info__container input:hover {
  border-color: #0266c1;
}
.info__container input:hover::placeholder {
  color: #0266c1;
}
.info__container input:focus {
  border: 1px solid #1c3374;
  outline: none;
}
.info__container input:focus::placeholder {
  color: rgba(255, 255, 255, 0);
  opacity: 0;
  transition: opacity 0.3s ease;
}
.info__container input:active {
  border: 1px solid #1c3374;
  outline: none;
}
.info__container input:active::placeholder {
  color: rgba(255, 255, 255, 0);
}
.info__container input:disabled {
  opacity: 0.1;
}
.info__container input:placeholder-shown {
  border-color: rgba(2, 102, 193, 0.1);
}
.info__container button {
  justify-content: center;
  width: 100%;
  padding: 21px;
  padding-top: 24px;
}
@media (max-width: 1199px) {
  .info__container button {
    margin-top: 8px;
    padding: 21px;
    padding-bottom: 19px;
  }
}
@media (max-width: 767px) {
  .info__container button {
    margin-top: 1px;
    padding: 18px 21px;
    padding-bottom: 16px;
  }
}

@media (max-width: 1199px) {
  .info__input-container {
    margin-bottom: 7px;
  }
}

.info__input-container.is-invalid input {
  border-color: #ff121f;
}

.contacts__container {
  display: grid;
  grid-template-columns: 40.5% auto;
  column-gap: 129px;
  padding-top: 83px;
  padding-bottom: 103px;
}
@media (max-width: 1439px) {
  .contacts__container {
    grid-template-columns: 42.5% auto;
  }
}
@media (max-width: 1199px) {
  .contacts__container {
    display: flex;
    flex-direction: column;
    gap: 40px 0;
    padding-top: 88px;
    padding-bottom: 69px;
  }
}
@media (max-width: 767px) {
  .contacts__container {
    gap: 30px 0;
    padding-top: 67px;
    padding-bottom: 29px;
  }
}

.contacts__description h2 {
  margin: 0;
  margin-bottom: 23px;
}
@media (max-width: 1199px) {
  .contacts__description h2 {
    margin-bottom: 18px;
  }
}
@media (max-width: 767px) {
  .contacts__description h2 {
    margin-bottom: 12px;
  }
}
.contacts__description h2::before {
  left: 0;
}
.contacts__description p {
  margin: 0;
  margin-bottom: 49px;
  font-size: 16px;
  line-height: 24px;
}
@media (max-width: 1199px) {
  .contacts__description p {
    margin-bottom: 39px;
    padding-right: 100px;
  }
}
@media (max-width: 767px) {
  .contacts__description p {
    margin-bottom: 29px;
    padding-right: 5px;
    font-size: 14px;
    line-height: 20px;
  }
}

.contacts__list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  row-gap: 35px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (max-width: 1199px) {
  .contacts__list {
    flex-direction: row;
    justify-content: space-between;
    gap: 0 44px;
    padding-right: 1px;
  }
}
@media (max-width: 767px) {
  .contacts__list {
    flex-direction: column;
    gap: 30px 0;
    padding-right: 0;
  }
}

.contacts__item {
  display: grid;
  grid-template-columns: min-content auto;
  gap: 8px 30px;
}
@media (max-width: 1199px) {
  .contacts__item {
    gap: 6px 10px;
  }
}
@media (max-width: 767px) {
  .contacts__item {
    row-gap: 8px;
    padding-right: 70px;
  }
}
.contacts__item h3 {
  grid-column: 2/-1;
  grid-row: 1/2;
  margin: 0;
  font-size: 18px;
  line-height: 18px;
  font-weight: 600;
}
@media (max-width: 1199px) {
  .contacts__item h3 {
    font-size: 16px;
    line-height: 16px;
  }
}
@media (max-width: 767px) {
  .contacts__item h3 {
    font-size: 14px;
    line-height: 14px;
  }
}
.contacts__item .contacts__link {
  grid-row: 2/3;
  display: flex;
  font-size: 16px;
  line-height: 24px;
}
@media (max-width: 1199px) {
  .contacts__item .contacts__link {
    font-size: 14px;
    line-height: 22px;
  }
}
@media (max-width: 767px) {
  .contacts__item .contacts__link {
    line-height: 20px;
  }
}
.contacts__item .contacts__link address {
  font-style: normal;
}
.contacts__item a:hover, .contacts__item a:focus {
  color: #1c3374;
  outline: none;
}
.contacts__item a:active {
  color: #0266c1;
}
.contacts__item svg {
  grid-column: 1/2;
  grid-row: 1/3;
  width: 26px;
  height: 26px;
  margin-top: 12px;
}
@media (max-width: 1199px) {
  .contacts__item svg {
    width: 24px;
    height: 24px;
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .contacts__item svg {
    width: 22px;
    height: 22px;
  }
}

.contacts__map-container {
  position: relative;
  width: 100%;
  min-height: 324px;
  margin-top: 75px;
}
@media (max-width: 1199px) {
  .contacts__map-container {
    min-height: 340px;
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .contacts__map-container {
    min-height: 320px;
  }
}

.contacts__map {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(2, 102, 193, 0.1);
}
@media (max-width: 1199px) {
  .contacts__map {
    min-height: 340px;
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .contacts__map {
    min-height: 320px;
  }
}

